<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <!-- build button for edit section  -->
      <v-btn
        class="ma-2"
        outlined
        small
        fab
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-icon>mdi-pencil-outline</v-icon>
      </v-btn>
      <!-- end  build button for update section  -->
    </template>

    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <!-- title card dialog  -->
        <v-card-title>
          <span class="text-h5">Edit Project</span>
        </v-card-title>
        <!-- title field card  -->
        <v-col cols="12">
          <v-text-field
            :rules="titleRules"
            label="Project Title Ar *"
            required
            v-model="project.title.ar"
          ></v-text-field>
        </v-col>
        <v-col cols="12">
          <v-text-field
            :rules="titleRules"
            label="Project Title En *"
            required
            v-model="project.title.en"
          ></v-text-field>
        </v-col>
        <v-col cols="12">
          <v-text-field
            :rules="titleRules"
            label="Project Title Tr *"
            required
            v-model="project.title.tr"
          ></v-text-field>
        </v-col>
        <!--  -->
        <!-- Description field card  -->
        <v-col cols="12">
          <v-textarea
            label="Project Description Ar *"
            v-model="project.description.ar"
            :rules="descriptionRules"
          >
          </v-textarea>
        </v-col>
        <v-col cols="12">
          <v-textarea
            label="Project Description En *"
            v-model="project.description.en"
            :rules="descriptionRules"
          >
          </v-textarea>
        </v-col>
        <v-col cols="12">
          <v-textarea
            label="Project Description Tr *"
            v-model="project.description.tr"
            :rules="descriptionRules"
          >
          </v-textarea>
        </v-col>
        <!--  -->
        <!-- total amount  field card  -->
        <v-col cols="12">
          <v-text-field
            prepend-inner-icon="mdi-currency-usd"
            label="Total Amount *"
            v-model="project.total_amount"
            :rules="totalAmountRules"
          >
          </v-text-field>
        </v-col>
        <!--  -->
        <!-- selection field from section  -->
        <v-col cols="12">
          <v-autocomplete
            :rules="sectionRules"
            v-model="section"
            label="section "
            :items="
              this.$store.getters.sections.map((item) => {
                return {
                  name: item.name[lang],
                  id: item.id,
                };
              })
            "
            item-text="name"
            item-value="id"
          >
            <template v-slot:selection="data">
              <v-chip
                class="primary"
                v-bind="data.attrs"
                :input-value="data.name"
                close
                @click="data.select"
                @click:close="removeSection(data.item)"
              >
                {{ data.item.name }}
              </v-chip>
            </template>
          </v-autocomplete>
        </v-col>
        <!--end build  selection field from section  -->

        <!-- selection field from campings  -->
        <v-col cols="12">
          <v-autocomplete
            :rules="campingRules"
            v-model="camping"
            label="camping "
            :items="
              this.$store.getters.campings.map((item) => {
                return {
                  title: item.title[this.lang],
                  id: item.id,
                };
              })
            "
            item-text="title"
            item-value="id"
          >
            <template v-slot:selection="data">
              <v-chip
                class="primary"
                v-bind="data.attrs"
                :input-value="data.name"
                close
                @click="data.select"
                @click:close="removeCamping(data.item)"
              >
                {{ data.item.title }}
              </v-chip>
            </template>
          </v-autocomplete>
        </v-col>
        <!--end build  selection field from campings  -->

        <!-- selection field from areas  -->

        <v-col cols="12">
          <v-autocomplete
            :rules="areasRules"
            v-model="areas"
            label="Implementation areas"
            multiple
            :items="
              this.$store.getters.areas.map((item) => {
                return {
                  name: item.name[this.lang],
                  id: item.id,
                };
              })
            "
            item-text="name"
            item-value="id"
          >
            <template v-slot:selection="data">
              <v-chip
                class="primary"
                v-bind="data.attrs"
                :input-value="data.name"
                close
                @click="data.select"
                @click:close="removeArea(data.item)"
              >
                {{ data.item.name }}
              </v-chip>
            </template>
          </v-autocomplete>
        </v-col>
        <!--end build  selection field from areas  -->

        <v-hover v-slot="{ hover }" open-delay="1000">
          <v-col cols="12">
            <v-text-field
              label="Project Video"
              required
              v-model="video"
            ></v-text-field>

            <v-container v-html="video" v-if="video && hover" class="video">
            </v-container>
          </v-col>
        </v-hover>

        <v-col cols="12">
          <v-file-input
            :rules="imageRules"
            v-model="file"
            chips
            accept="image/png, image/jpeg"
            label="Image"
            @change="onFileChange"
        /></v-col>
        <v-col cols="12">
          <v-img
            contain
            max-height="250"
            aspect-ratio="1"
            :lazy-src="image ? image : minUrlImage + projectData.image_url"
            :src="image ? image : minUrlImage + projectData.image_url"
          >
          </v-img>
        </v-col>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="restData"> Cancel </v-btn>
          <v-btn color="secondary" text @click="updateProject">
            <span v-if="!progressUpdateProject">Update Project</span>
            <v-progress-circular
              v-if="progressUpdateProject"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>

<script>
import { projectController } from "../../components/projects/project";
export default {
  props: ["projectData"],
  watch: {
    dialog(state) {
      if (state) {
        this.video = this.projectData.vedio_url;
        this.camping = this.projectData.campaign_id;
        this.projectData.areas.forEach((area) => {
          this.areas.push(area.id);
        });
      }
    },
  },
  data() {
    return {
      video: null,
      image: null,
      file: null,
      dialog: false,
      progressUpdateProject: false,
      valid: true,
      project: this.projectData,
      areas: [],
      section: this.projectData.section_id,
      camping: null,
      totalAmountRules: [
        (v) => !!v || "type the total amount ",
        (v) => this.isNumeric(v) || "type number valed ",
      ],
      minUrlImage: this.$store.state.imageUlr + "project/",
      titleRules: [(v) => !!v || "the title field is required"],
      descriptionRules: [(v) => !!v || "Description is required"],
      imageRules: [],
      areasRules: [(v) => !!(v && v.length) || "Choose at least areas "],
      sectionRules: [(v) => !!v || "Choose at least section"],
      campingRules: [],
    };
  },
  computed: {
    lang() {
      return this.$store.getters.lang;
    },
  },
  methods: {
    isNumeric: function (n) {
      return !isNaN(parseFloat(n)) && isFinite(n);
    },
    updateProject: async function () {
      //   (file, project, areas, camping_id, section_id, video)
      if (this.$refs.form.validate()) {
        this.progressUpdateProject = true;
        var state = await projectController.updateProject(
          this.file,
          this.project,
          this.areas,
          this.camping,
          this.section,
          this.video
        );
        this.progressUpdateProject = false;
        if (state) {
          this.restData();
        }
        this.$emit("getVal");
      }
    },
    removeArea(item) {
      console.log(this.areas);
      const index = this.areas.indexOf(item.id);

      if (index >= 0) this.areas.splice(index, 1);
    },
    removeSection() {
      this.section = null;
    },
    removeCamping() {
      this.camping = null;
    },
    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },
    restData() {
      // this.$refs.form.reset();
      this.valid = true;
      this.dialog = false;
    },
  },
};
</script>

<style scoped>
.video {
  z-index: 1023;
  right: 20px;
  bottom: 10%;
  position: absolute;
}
</style>